}

html {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 500;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Magistral", sans-serif;
  background-color: #e8f9ff;
  color: #021f29;
  font-size: 16px;
  line-height: 1.3;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  position: relative;
  z-index: 1;
}

section {
  transition: opacity 1s ease;
}

a {
  color: #021f29;
  text-decoration: none;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

.d-none-desktop {
  display: none !important;
}
@media only screen and (max-width: 991px) {
  .d-none-desktop {
    display: block !important;
  }
}

.d-none-mobile {
  display: block !important;
}
@media only screen and (max-width: 991px) {
  .d-none-mobile {
    display: none !important;
  }
}

.d-none-tablet {
  display: none !important;
}
@media only screen and (max-width: 767px) {
  .d-none-tablet {
    display: inline-flex !important;
  }
}

.d-none-mobile-tablet {
  display: inline-flex !important;
}
@media only screen and (max-width: 767px) {
  .d-none-mobile-tablet {
    display: none !important;
  }
}

[hidden] {
  display: none !important;
}

/* Mobile-menu */
[data-state=mobile-menu] {
  overflow: hidden;
}
[data-state=mobile-menu] .header__menu-wrapper {
  transform: translateX(0);
}

/* When hystmodal opened */
.hystmodal__opened .header {
  transform: translateY(-100%);
}

/* Swiper */
.swiper-nav {
  position: absolute;
  top: 50%;
  left: 0;
  z-index: 10;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  pointer-events: none;
}
@media only screen and (max-width: 767px) {
  .swiper-nav {
    display: none;
  }
}
.swiper-nav .swiper-btn-prev,
.swiper-nav .swiper-btn-next {
  position: relative;
  cursor: pointer;
  pointer-events: auto;
}
.swiper-nav .swiper-btn-prev:hover svg,
.swiper-nav .swiper-btn-next:hover svg {
  color: rgba(70, 207, 255, 0.7);
}
.swiper-nav .swiper-btn-prev svg,
.swiper-nav .swiper-btn-next svg {
  color: #46cfff;
  transition: 0.3s ease;
}

.swiper-pagination {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 36px;
}
@media only screen and (max-width: 991px) {
  .swiper-pagination {
    gap: 20px;
  }
}
@media only screen and (max-width: 767px) {
  .swiper-pagination {
    gap: 10px;
  }
}
.swiper-pagination__bullet {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 94px;
  height: 94px;
  background: transparent;
  opacity: 1;
  border-radius: 28px;
  border: 4px solid #46cfff;
  cursor: pointer;
  transition: 0.3s ease;
}
@media only screen and (max-width: 991px) {
  .swiper-pagination__bullet {
    width: 70px;
    height: 70px;
    border-radius: 24px;
  }
}
@media only screen and (max-width: 767px) {
  .swiper-pagination__bullet {
    width: 50px;
    height: 50px;
    border-radius: 18px;
    border: 2px solid #46cfff;
  }
}
.swiper-pagination__bullet:hover {
  background-color: #46cfff;
}
.swiper-pagination__bullet:hover svg {
  color: #ffffff;
}
.swiper-pagination__bullet svg {
  color: #46cfff;
  width: 75%;
  height: 75%;
  transition: 0.3s ease;
}
.swiper-pagination__bullet--active {
  background-color: #46cfff;
}
.swiper-pagination__bullet--active svg {
  color: #ffffff;
}
.swiper-pagination .swiper-pagination-bullet-active {
  width: 94px;
  height: 94px;
}

/* Select2 */
.js-custom-select ~ .select2-container {
  width: 100% !important;
}

.select2-container--default .select2-selection--single {
  height: auto;
  border: 1px solid #46cfff;
  border-radius: 30px;
  padding: 28px 0;
  background-color: transparent;
  transition: background-color 0.3s ease;
}
.select2-container--default .select2-selection--single:hover {
  background-color: rgba(70, 207, 255, 0.2);
}
.select2-container--default .select2-selection--single .select2-selection__rendered {
  font-family: "IBMPlexSans", sans-serif;
  color: #021f29;
  font-weight: 300;
  font-size: 16px;
  line-height: 1;
  padding: 0 70px 0 24px;
}
.select2-container--default .select2-selection--single .select2-selection__arrow {
  width: 44px;
  height: 19px;
  top: 50%;
  right: 23px;
  transform: translateY(-50%);
