/* Blocks */
.container {
  max-width: 1088px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  max-width: 336px;
  width: 100%;
  padding: 12px 20px;
  border-radius: 16px;
  background-color: #46cfff;
  color: #021f29;
  font-size: 18px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  transition: 0.3s ease;
}
.button:hover {
  background-color: rgba(70, 207, 255, 0.7);
}

.title {
  font-weight: 400;
  font-size: 46px;
  line-height: 1;
}
@media only screen and (max-width: 1279px) {
  .title {
    font-size: calc(30px + (46 - 30) * (100vw - 992px) / (1280 - 992));
  }
}
@media only screen and (max-width: 991px) {
  .title {
    font-size: calc(25px + (30 - 25) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .title {
    font-size: calc(20px + (30 - 20) * (100vw - 0px) / (768 - 0));
  }
}
.title--big {
  font-size: 50px;
}
@media only screen and (max-width: 1279px) {
  .title--big {
    font-size: calc(30px + (50 - 30) * (100vw - 992px) / (1280 - 992));
  }
}
@media only screen and (max-width: 991px) {
  .title--big {
    font-size: calc(25px + (30 - 25) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .title--big {
    font-size: calc(20px + (30 - 20) * (100vw - 0px) / (768 - 0));
  }
}
.title span {
  color: #46cfff;
}

.hystmodal__window {
  max-width: 1040px;
  width: 100%;
  border-radius: 30px;
  overflow: hidden;
}
.hystmodal:before {
  opacity: 1;
  background-color: rgba(2, 31, 41, 0.2);
}
.hystmodal__close {
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  padding: 0;
  cursor: pointer;
  background-image: unset;
  background-size: unset;
}
.hystmodal__close:focus {
  outline: none;
  outline-offset: unset;
}
.hystmodal__close:hover svg {
  color: #46cfff;
}
.hystmodal__close svg {
  transition: 0.3s ease;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  padding: 80px 0;
  overflow: hidden;
  transition: 0.5s ease;
}
@media only screen and (max-width: 767px) {
  .header {
    padding: 40px 0;
  }
}
.header--bg {
  background-color: #ffffff;
  box-shadow: 4px 4px 34px 0 rgba(2, 31, 41, 0.2);
  padding: 40px 0;
}
.header--hide {
  transform: translateY(-100%);
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.header__logos {
  display: flex;
  align-items: center;
  gap: 48px;
}
@media only screen and (max-width: 575px) {
  .header__logos {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }
}
.header__logo {
  transition: 0.3s ease;
}
.header__logo:hover {
  opacity: 0.7;
}
.header__logo svg {
  width: 153px;
  height: 23px;
}
.header__logo-cn {
  transition: 0.3s ease;
}
.header__logo-cn:hover {
  opacity: 0.7;
}
.header__logo-cn svg {
  width: 114px;
  height: 22px;
}
.header__menu-wrapper {
  transition: 0.3s ease;
}
@media only screen and (max-width: 767px) {
  .header__menu-wrapper {
    position: fixed;
    inset: 0;
    z-index: 10;
    background-color: #e8f9ff;
    transform: translateX(100%);
  }
}
.header__menu {
  display: flex;
  align-items: center;
  gap: 42px;
}
@media only screen and (max-width: 767px) {
  .header__menu {
    flex-direction: column;
    justify-content: center;
    height: 100%;
  }
}
.header__menu li a {
  position: relative;
  font-family: "IBMPlexSans", sans-serif;
  font-weight: 300;
}
@media only screen and (max-width: 767px) {
  .header__menu li a {
    font-weight: 700;
    font-size: 26px;
    text-transform: uppercase;
  }
}
.header__menu li a::before {
  content: "";
  position: absolute;
  width: 0;
  inset: -5px -5px -10px;
  border-radius: 5px;
  background-color: #46cfff;
  transition: width 0.2s ease, top 0.2s ease 0.2s;
}
@media only screen and (max-width: 767px) {
  .header__menu li a::before {
    width: calc(100% + 10px);
    top: 23px;
