@media only screen and (max-width: 767px) {
  .article-block__main {
    margin-top: 0;
  }
}
.article-block__link {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  font-family: "IBMPlexSans", sans-serif;
  font-weight: 700;
  color: #46cfff;
  font-size: 16px;
  line-height: 1.3;
  text-decoration: none;
  margin-top: 75px;
}
@media only screen and (max-width: 575px) {
  .article-block__link {
    margin-top: 40px;
  }
}
.article-block__link:hover svg {
  transform: translateX(5px);
}
.article-block__link span {
  position: relative;
  z-index: 1;
}
.article-block__link svg {
  transform: translateX(0);
  transition: 0.3s ease;
}
.article-block__tags {
  margin-bottom: 78px;
}
.article-block__title {
  font-size: 46px;
  margin-bottom: 56px;
}
@media only screen and (max-width: 991px) {
  .article-block__title {
    font-size: 38px;
  }
}
@media only screen and (max-width: 767px) {
  .article-block__title {
    font-size: 30px;
  }
}
@media only screen and (max-width: 575px) {
  .article-block__title {
    font-size: 24px;
  }
}